.info {
    display: flex;
    margin: 20px 0 0 0;
}
.info_img {
    width: 150px;
    height: 150px;
    margin: 0 20px 0 0;
    flex: 0 0 auto;
}
.info_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.info_name {
    font-size: 26px;
    margin: 0 0 30px;
}
